'use client'

import { useRouter } from 'next/navigation'
import { Card, CardContent } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'

interface Chat {
  id: string
  name: string
  participants: string[]
  lastMessage: string
  lastMessageTime: string
  unreadCount: number
}

interface ChatListProps {
  chats: Chat[]
  currentUser: string
}

export function ChatList({ chats, currentUser }: ChatListProps) {
  const router = useRouter()

  const handleChatClick = (chatId: string) => {
    router.push(`/chats/${chatId}`)
  }

  const formatTime = (timestamp: string) => {
    try {
      const date = new Date(timestamp)
      const now = new Date()
      const diffInHours = (now.getTime() - date.getTime()) / (1000 * 60 * 60)

      if (diffInHours < 1) {
        const minutes = Math.floor(diffInHours * 60)
        return minutes <= 0 ? 'now' : `${minutes}m`
      } else if (diffInHours < 24) {
        return `${Math.floor(diffInHours)}h`
      } else {
        const days = Math.floor(diffInHours / 24)
        return `${days}d`
      }
    } catch (error) {
      return ''
    }
  }

  const getOtherParticipants = (participants: string[]) => {
    return participants.filter(p => p !== currentUser).join(', ') || 'You'
  }

  return (
    <div className="space-y-2">
      {chats.map((chat) => (
        <Card 
          key={chat.id} 
          className="cursor-pointer hover:bg-accent/50 transition-colors duration-200"
          onClick={() => handleChatClick(chat.id)}
        >
          <CardContent className="p-4">
            <div className="flex items-center justify-between">
              <div className="flex-1 min-w-0">
                <div className="flex items-center justify-between mb-1">
                  <h3 className="font-semibold text-foreground truncate">
                    {chat.name}
                  </h3>
                  <div className="flex items-center space-x-2 ml-2">
                    {chat.unreadCount > 0 && (
                      <Badge variant="default" className="text-xs px-2 py-1">
                        {chat.unreadCount}
                      </Badge>
                    )}
                    <span className="text-xs text-muted-foreground whitespace-nowrap">
                      {formatTime(chat.lastMessageTime)}
                    </span>
                  </div>
                </div>
                <div className="flex items-center justify-between">
                  <p className="text-sm text-muted-foreground truncate flex-1">
                    {chat.lastMessage || 'No messages yet'}
                  </p>
                </div>
                <p className="text-xs text-muted-foreground mt-1">
                  with {getOtherParticipants(chat.participants)}
                </p>
              </div>
            </div>
          </CardContent>
        </Card>
      ))}
    </div>
  )
}
