'use client'

import { useEffect, useState } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { toast } from 'sonner'
import { useNotifications } from '@/hooks/useNotifications'

export function NotificationPermission() {
  const [showPrompt, setShowPrompt] = useState(false)
  const [isRequesting, setIsRequesting] = useState(false)
  const { requestNotificationPermission, notificationStatus } = useNotifications()

  useEffect(() => {
    // Show prompt if notifications are not granted and not denied
    if (notificationStatus === 'default') {
      setShowPrompt(true)
    }
  }, [notificationStatus])

  const handleRequestPermission = async () => {
    setIsRequesting(true)
    try {
      const permission = await requestNotificationPermission()
      if (permission === 'granted') {
        toast.success('Notifications enabled! You\'ll receive alerts for new messages.')
        setShowPrompt(false)
      } else if (permission === 'denied') {
        toast.error('Notifications blocked. You can enable them in your browser settings.')
        setShowPrompt(false)
      }
    } catch (error) {
      console.error('Error requesting notification permission:', error)
      toast.error('Failed to request notification permission')
    } finally {
      setIsRequesting(false)
    }
  }

  const handleDismiss = () => {
    setShowPrompt(false)
    toast.info('You can enable notifications later in your browser settings')
  }

  if (!showPrompt || notificationStatus === 'granted') {
    return null
  }

  return (
    <div className="fixed top-4 left-4 right-4 z-50 md:left-auto md:right-4 md:w-96">
      <Card className="border-primary/20 bg-card/95 backdrop-blur-sm">
        <CardHeader className="pb-3">
          <CardTitle className="text-sm font-semibold">Enable Notifications</CardTitle>
          <CardDescription className="text-xs">
            Get notified when you receive new messages in Nel'sapp
          </CardDescription>
        </CardHeader>
        <CardContent className="pt-0">
          <div className="flex space-x-2">
            <Button 
              size="sm" 
              onClick={handleRequestPermission}
              disabled={isRequesting}
              className="flex-1"
            >
              {isRequesting ? 'Requesting...' : 'Enable'}
            </Button>
            <Button 
              size="sm" 
              variant="outline" 
              onClick={handleDismiss}
              disabled={isRequesting}
            >
              Later
            </Button>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
