'use client'

import { useEffect, useState } from 'react'
import { useRouter } from 'next/navigation'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Card, CardContent } from '@/components/ui/card'
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog'
import { Label } from '@/components/ui/label'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { toast } from 'sonner'
import { ChatList } from '@/components/ChatList'
import { NotificationPermission } from '@/components/NotificationPermission'

interface User {
  username: string
  id: string
  joinedAt: string
}

interface Chat {
  id: string
  name: string
  participants: string[]
  lastMessage: string
  lastMessageTime: string
  unreadCount: number
  type?: string
}

export default function ChatsPage() {
  const [user, setUser] = useState<User | null>(null)
  const [chats, setChats] = useState<Chat[]>([])
  const [privateChats, setPrivateChats] = useState<Chat[]>([])
  const [isLoading, setIsLoading] = useState(true)
  const [newChatName, setNewChatName] = useState('')
  const [newChatParticipant, setNewChatParticipant] = useState('')
  const [isCreatingChat, setIsCreatingChat] = useState(false)
  const [isDialogOpen, setIsDialogOpen] = useState(false)
  const [isCreatingInvite, setIsCreatingInvite] = useState(false)
  const [inviteLink, setInviteLink] = useState('')
  const [showInviteDialog, setShowInviteDialog] = useState(false)
  const router = useRouter()

  useEffect(() => {
    const checkAuth = () => {
      try {
        const userData = localStorage.getItem('nelsapp_user')
        if (!userData) {
          router.push('/login')
          return
        }
        setUser(JSON.parse(userData))
      } catch (error) {
        console.error('Auth check error:', error)
        router.push('/login')
      }
    }

    checkAuth()
  }, [router])

  useEffect(() => {
    const fetchChats = async () => {
      try {
        // Fetch group chats
        const groupResponse = await fetch('/api/chats')
        if (groupResponse.ok) {
          const groupData = await groupResponse.json()
          setChats(groupData.chats || [])
        }

        // Fetch private chats
        const privateResponse = await fetch(`/api/chats/private?username=${user?.username}`)
        if (privateResponse.ok) {
          const privateData = await privateResponse.json()
          setPrivateChats(privateData.chats || [])
        }
      } catch (error) {
        console.error('Error fetching chats:', error)
        toast.error('Failed to load chats')
      } finally {
        setIsLoading(false)
      }
    }

    if (user) {
      fetchChats()
    }
  }, [user])

  const handleCreateInvite = async () => {
    if (!user) return

    setIsCreatingInvite(true)
    try {
      const response = await fetch('/api/invites', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          createdBy: user.username
        }),
      })

      if (response.ok) {
        const data = await response.json()
        setInviteLink(data.inviteLink)
        setShowInviteDialog(true)
        toast.success('Invitation link created!')
      } else {
        const error = await response.json()
        toast.error(error.message || 'Failed to create invitation')
      }
    } catch (error) {
      console.error('Error creating invite:', error)
      toast.error('Failed to create invitation')
    } finally {
      setIsCreatingInvite(false)
    }
  }

  const copyInviteLink = async () => {
    try {
      await navigator.clipboard.writeText(inviteLink)
      toast.success('Invitation link copied to clipboard!')
    } catch (error) {
      console.error('Error copying to clipboard:', error)
      toast.error('Failed to copy link')
    }
  }

  const handleCreateChat = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!newChatName.trim()) {
      toast.error('Please enter a chat name')
      return
    }

    if (!newChatParticipant.trim()) {
      toast.error('Please enter a participant username')
      return
    }

    setIsCreatingChat(true)

    try {
      const response = await fetch('/api/chats', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          name: newChatName.trim(),
          participants: [user?.username, newChatParticipant.trim()],
          createdBy: user?.username
        }),
      })

      if (response.ok) {
        const data = await response.json()
        setChats(prev => [data.chat, ...prev])
        setNewChatName('')
        setNewChatParticipant('')
        setIsDialogOpen(false)
        toast.success('Chat created successfully!')
      } else {
        const error = await response.json()
        toast.error(error.message || 'Failed to create chat')
      }
    } catch (error) {
      console.error('Error creating chat:', error)
      toast.error('Failed to create chat')
    } finally {
      setIsCreatingChat(false)
    }
  }

  const handleLogout = () => {
    try {
      localStorage.removeItem('nelsapp_user')
      toast.success('Logged out successfully')
      router.push('/login')
    } catch (error) {
      console.error('Logout error:', error)
      toast.error('Failed to logout')
    }
  }

  if (!user) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="w-8 h-8 border-4 border-primary border-t-transparent rounded-full animate-spin"></div>
      </div>
    )
  }

  const allChats = [...privateChats, ...chats]

  return (
    <div className="min-h-screen bg-background">
      <NotificationPermission />
      
      {/* Header */}
      <div className="sticky top-0 z-10 bg-background border-b border-border">
        <div className="flex items-center justify-between p-4">
          <div>
            <h1 className="text-2xl font-bold">Nel'sapp</h1>
            <p className="text-sm text-muted-foreground">Welcome, {user.username}</p>
          </div>
          <div className="flex items-center space-x-2">
            <Button 
              variant="default" 
              size="sm" 
              onClick={handleCreateInvite}
              disabled={isCreatingInvite}
            >
              {isCreatingInvite ? 'Creating...' : 'Invite Friend'}
            </Button>
            <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
              <DialogTrigger asChild>
                <Button variant="outline" size="sm">
                  New Group
                </Button>
              </DialogTrigger>
              <DialogContent className="sm:max-w-md">
                <DialogHeader>
                  <DialogTitle>Create New Group Chat</DialogTitle>
                  <DialogDescription>
                    Start a group conversation by adding participants
                  </DialogDescription>
                </DialogHeader>
                <form onSubmit={handleCreateChat} className="space-y-4">
                  <div className="space-y-2">
                    <Label htmlFor="chatName">Group Name</Label>
                    <Input
                      id="chatName"
                      placeholder="Enter group name"
                      value={newChatName}
                      onChange={(e) => setNewChatName(e.target.value)}
                      disabled={isCreatingChat}
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="participant">Add Participant</Label>
                    <Input
                      id="participant"
                      placeholder="Enter username to add"
                      value={newChatParticipant}
                      onChange={(e) => setNewChatParticipant(e.target.value)}
                      disabled={isCreatingChat}
                    />
                  </div>
                  <div className="flex justify-end space-x-2">
                    <Button
                      type="button"
                      variant="outline"
                      onClick={() => setIsDialogOpen(false)}
                      disabled={isCreatingChat}
                    >
                      Cancel
                    </Button>
                    <Button type="submit" disabled={isCreatingChat}>
                      {isCreatingChat ? 'Creating...' : 'Create Group'}
                    </Button>
                  </div>
                </form>
              </DialogContent>
            </Dialog>
            <Button 
              variant="outline" 
              size="sm" 
              onClick={() => router.push('/privacy')}
            >
              Privacy
            </Button>
            <Button variant="outline" size="sm" onClick={handleLogout}>
              Logout
            </Button>
          </div>
        </div>
      </div>

      {/* Invite Link Dialog */}
      <Dialog open={showInviteDialog} onOpenChange={setShowInviteDialog}>
        <DialogContent className="sm:max-w-md">
          <DialogHeader>
            <DialogTitle>Invitation Link Created</DialogTitle>
            <DialogDescription>
              Share this link with someone to start a private chat
            </DialogDescription>
          </DialogHeader>
          <div className="space-y-4">
            <div className="p-3 bg-muted rounded-lg">
              <p className="text-sm font-mono break-all">{inviteLink}</p>
            </div>
            <div className="flex justify-end space-x-2">
              <Button
                variant="outline"
                onClick={() => setShowInviteDialog(false)}
              >
                Close
              </Button>
              <Button onClick={copyInviteLink}>
                Copy Link
              </Button>
            </div>
            <div className="text-xs text-muted-foreground">
              This link will expire in 7 days and can only be used once.
            </div>
          </div>
        </DialogContent>
      </Dialog>

      {/* Chat List */}
      <div className="p-4">
        {isLoading ? (
          <div className="flex items-center justify-center py-8">
            <div className="w-8 h-8 border-4 border-primary border-t-transparent rounded-full animate-spin"></div>
          </div>
        ) : allChats.length === 0 ? (
          <Card>
            <CardContent className="flex flex-col items-center justify-center py-8 text-center">
              <h3 className="text-lg font-semibold mb-2">No chats yet</h3>
              <p className="text-muted-foreground mb-4">
                Invite a friend for private chat or create a group to start messaging
              </p>
              <div className="flex space-x-2">
                <Button onClick={handleCreateInvite}>
                  Invite Friend
                </Button>
                <Button variant="outline" onClick={() => setIsDialogOpen(true)}>
                  Create Group
                </Button>
              </div>
            </CardContent>
          </Card>
        ) : (
          <Tabs defaultValue="all" className="w-full">
            <TabsList className="grid w-full grid-cols-3">
              <TabsTrigger value="all">All Chats ({allChats.length})</TabsTrigger>
              <TabsTrigger value="private">Private ({privateChats.length})</TabsTrigger>
              <TabsTrigger value="groups">Groups ({chats.length})</TabsTrigger>
            </TabsList>
            <TabsContent value="all" className="mt-4">
              <ChatList chats={allChats} currentUser={user.username} />
            </TabsContent>
            <TabsContent value="private" className="mt-4">
              {privateChats.length === 0 ? (
                <Card>
                  <CardContent className="flex flex-col items-center justify-center py-8 text-center">
                    <h3 className="text-lg font-semibold mb-2">No private chats</h3>
                    <p className="text-muted-foreground mb-4">
                      Invite someone to start a private conversation
                    </p>
                    <Button onClick={handleCreateInvite}>
                      Invite Friend
                    </Button>
                  </CardContent>
                </Card>
              ) : (
                <ChatList chats={privateChats} currentUser={user.username} />
              )}
            </TabsContent>
            <TabsContent value="groups" className="mt-4">
              {chats.length === 0 ? (
                <Card>
                  <CardContent className="flex flex-col items-center justify-center py-8 text-center">
                    <h3 className="text-lg font-semibold mb-2">No group chats</h3>
                    <p className="text-muted-foreground mb-4">
                      Create a group to chat with multiple people
                    </p>
                    <Button onClick={() => setIsDialogOpen(true)}>
                      Create Group
                    </Button>
                  </CardContent>
                </Card>
              ) : (
                <ChatList chats={chats} currentUser={user.username} />
              )}
            </TabsContent>
          </Tabs>
        )}
      </div>
    </div>
  )
}
